import pickRegions from '@/components/pick-regions/pick-regions.vue';
import {post,toast} from '@/common/index';
export default {
	components:{
	    pickRegions
	},
	data() {
		return {
			regions:[],
			defaultRegions:['湖南省','长沙市','岳麓区'],
			statusBarHeight:getApp().globalData.statusBarHeight,
			titile:'',
			region:'',
			data:{
				detailedAddress:"",
				isDefault:0,
				phone:"",
				consignee:"",
				userId:'',
				province:'',
				city:'',
				county:''
			},
			title:"添加地址",
			signs:false
		}
	},
	onLoad(e) {
		this.data={
			detailedAddress:"",
			isDefault:0,
			phone:"",
			consignee:"",
			userId:'',
			province:'',
			city:'',
			county:''
		}
		if(e.id){
			this.signs=true;
			this.title="修改地址";
			this.data=e;
			this.defaultRegions=[e.province,e.city,e.county];
			this.region=e.province+e.city+e.county
		}else{
			this.signs=false;
			this.title="添加地址"
		}
	},
	methods: {
		handleGetRegions(regions){
			this.region=regions.map(item=>item.name);
			this.data.province=this.region[0];
			this.data.city=this.region[1];
			this.data.county=this.region[2];
		},
		goBack:function(){
			uni.navigateBack({
			    delta: 1
			});
		},
		del:function(){
			post('/api/user/shippingaddress/delete',this.data.id).then((res)=>{
				if(res[1].data.code==200){
					toast("删除成功")
					setTimeout(function(){
						uni.navigateBack({
						    delta: 1
						});
					},1000)
				}else{
					toast(res[1].data.msg)
				}
			}).catch((res)=>{
				toast(res[1].data.msg)
			})
		},
		switch1Change:function(e){
			if(e.target.value){
				this.data.isDefault=1;
			}else{
				this.data.isDefault=0;
			}
		},
		add:function(){
			if(this.data.consignee==''){
				toast('收货人不能为空')
			}else if(this.data.phone==''){
				toast('手机号码不能为空')
			}else if(!(/^1\d{10}$/).test(this.data.phone)){
				toast('手机号码不存在')
			}else  if(this.region==''){
				toast('地区不能为空')
			}else if(this.data.detailedAddress==''){
				toast('详细地址不能为空')
			}else{
				if(this.signs){
					post('/api/user/shippingaddress/update',this.data).then((res)=>{
						if(res[1].data.code==200){
							uni.navigateBack({
							    delta: 1
							});
						}else{
							toast(res[1].data.msg)
						}
					}).catch((res)=>{
						toast(res[1].data.msg)
					})
				}else{
					this.data.userId=uni.getStorageSync('userId');
					post('/api/user/shippingaddress/save',this.data).then((res)=>{
						if(res[1].data.code==200){
							uni.navigateBack({
								delta: 1
							});
						}else{
							toast(res[1].data.msg)
						}
					}).catch((res)=>{
						toast(res[1].data.msg)
					})
				}
			}
			
		}
	}
}
